// excel
import XLSX from 'xlsx'
/**
 * @param dataList   array
 * @param fileName  .xlsx
 */
export const downloadXlsx = (dataList, fileName) => {
    // dataList =  [[],[]]
   
    const stringToBuff = str => {
        var buf = new ArrayBuffer(str.length)
        var view = new Uint8Array(buf)
        for (var i = 0; i !== str.length; ++i) {
            view[i] = str.charCodeAt(i) & 0xff
        }
        return buf
    }
    // 
    let workbook = XLSX.utils.book_new()
    let worksheet = XLSX.utils.aoa_to_sheet(dataList)
    XLSX.utils.book_append_sheet(workbook, worksheet, 'sheet1')

    // 
    let xlsxBlob = new Blob(
        [
            stringToBuff(
                XLSX.write(workbook, {
                    bookType: 'xlsx',
                    bookSST: false,
                    type: 'binary'
                })
            )
        ],
        { type: '' }
    )

    const a = document.createElement('a')
    // URL.createObjectURL()ablob URL
    a.href = URL.createObjectURL(xlsxBlob) // 
    a.download = fileName
    a.click()
}


